* {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}

body {
    margin: 0;
    font-size: 14px;
    background-color: #f8f8f8;
}

ul{
    list-style: none;
}

li{
list-style-type:none;
}

a,label {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
}

img{
    width: 100%;
    height: 100%;
}

.space{
    height: 1.2rem;
    background-color: #f8f8f8;
}

.pdt0{
    padding-top: 0 !important;
}

.bdb0 {
 border-bottom-width: 0px !important;
}

div.bdt0 {
    border-top-width: 0px !important;
}

.mb5{
    margin-bottom: 5rem !important;
}

.hid{
    display: none !important;
}

.wa{
    width: auto !important;
}


#app {
    cursor: pointer;
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}

#app_wrapper > .pd{
    padding-left: 1rem;
    padding-right: 1rem;
}

.lumos-tabbody{
    overflow: auto;
    height: 100%;
    padding-bottom: 100px;
}

.lumos-button{
        border: 1px solid transparent;
        border-color: transparent;
        background-color: #01b9c5;
        color: #fff;
        position: relative;
        display: inline-block;
        margin: 0;
        padding: 0 12px;
        min-width: 52px;
        border-radius: 4px;
        vertical-align: top;
        text-align: center;
        text-overflow: ellipsis;
        font-size: 16px;
        cursor: pointer;
}

.lumos-button.lumos-button-bottom{
    position: fixed;
    bottom: 0;
    width: 100%;
    clear: both;
    display: block;
    border-radius: 0;
    height: 46px;
    line-height: 46px;
}

.lumos-button.lumos-button-full{
    height: 46px;
    line-height: 46px;
    width: 100%;
}

.lumos-button.lumos-button-private{
   height: 28px;
   font-size: .8rem;
}




